.complex-list__title {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 24px;
}

.complex-list {
    padding: 38px 0 48px;
}

.complex-item {
    display: grid;
    grid-template-columns: 1.55fr 4fr;
    border: 1px solid #EDEDED;
    border-radius: 10px;
    margin-bottom: 30px;
    @media(max-width: 1200px) {
        grid-template-columns: 1fr;
    }
    @include sm-block {
        margin-bottom: 18px;
    }
}

.complex-item__gallery {
    align-self: flex-start;
    overflow: hidden;
    position: relative;
    height: 100%;
    border-radius: 10px 0 0 10px;
    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .swiper-pagination-bullet {
        background-color: $color-white;
        opacity: 1;
        &-active {
            background-color: $color-primary;
        }
    }
    @media(max-width: 1200px) {
        max-width: 495px;
        overflow: hidden;
        border-radius: 10px;
    }
    @include sm-block {
        max-width: 100%;
        height: 255px;
        border-radius: 10px 10px 0 0;

    }
}

.complex-item__main {
    display: flex;
    @include sm-block {
        flex-direction: column;
        padding: 26px 22px 34px;
    }
}

.complex-item__info {
    padding: 30px 38px;
    @include sm-block {
        padding: 0;
    }
}

.complex-item__name {
    margin-bottom: 14px;
    font-size: 24px;
    line-height: 30px;
    @include sm-block {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 19px;
    }
}

.complex-item__adress {
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 18px;
    margin-bottom: 17px;
    svg {
        display: block;
        margin-right: 6px;
    }
    @include sm-block {
        margin-bottom: 20px;
    }
}

.complex-item__subway {
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 18px;
    font-weight: 300;
    margin-bottom: 23px;
    svg {
        display: block;
        margin-right: 9px;
    }
}

.complex-item__complete {
    font-size: 15px;
    line-height: 18px;
    font-weight: 300;
    margin-bottom: 24px;
    color: $color-text;
    
    @include sm-block {
        margin-bottom: 26px;
        font-size: 13px;
        line-height: 15px;
    }
}

.complex-item__desc {
    font-size: 15px;
    line-height: 25px;
    margin-bottom: 19px;
    color: $color-text;
    @include sm-block {
        margin-bottom: 26px;
        font-size: 13px;
        line-height: 15px;
    }
}

.complex-item__controls {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
    @include sm-block {
        display: grid;
        gap: 14px;
        grid-gap: 14px;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-bottom: 36px;
        .btn {
            width: 100%;
            text-align: center;
        }
    }
}

.complex-item__call {
    max-width: 156px;
    width: 100%;
    @include sm-block {
        max-width: 100%;
    }
}

.complex-item__map {
    max-width: 153px;
    width: 100%;
    @include sm-block {
        max-width: 100%;
    }
}

.complex-item__flats {
    max-width: 382px;
    width: 100%;
    padding: 49px;
    border-left: 1px solid #F2F2F2;
    @include xl-block {
        padding: 32px;
    }
    @include sm-block {
        padding: 0;
        border-left: none;
    }
}

.complex-item__flat {
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 18px;
    font-weight: 300;
    white-space: nowrap;
    margin-bottom: 45px;
    color: $color-text;
    &:last-of-type {
        margin-bottom: 0;
    }
    @include md-block {
        margin-bottom: 24px;
    }
    @include sm-block { 
        justify-content: space-between;
        font-size: 12px;
        line-height: 14px;
        margin-bottom: 16px;
    }
}

.complex-item__flat-type {
    margin-right: 18px;
    color: $color-primary;
    @include sm-block {
        margin-right: 14px;
    }
}

.complex-item__flat-size {
    margin-right: 24px;
    @include sm-block {
        margin-right: 14px;
    }
}

.complex-list__pagination {
    margin: 53px 0 0 0;
    @include sm-block {
        margin-top: 27px;
    }
    img {
        display: block;
        margin: 0 auto;
    }
}