.filter {
    padding: 100px 0 102px;
    background: url('../images/filter-banner.png') center center / cover no-repeat;
    @include sm-block {
        padding: 54px 0 51px;
    }
}

.filter__title {
    font-size: 34px;
    line-height: 42px;
    margin-bottom: 80px;
    font-weight: 400;
    text-align: center;
    color: $color-white;
    @include sm-block {
        max-width: 271px;
        margin: 0 auto 26px;
        font-size: 22px;
        line-height: 33px;
    }
}

.filter-sections {
    display: flex;   
    gap: 3px;
    align-items: center;
    margin-bottom: 10px;
    overflow: hidden;
    border-radius: 10px;
    @media(max-width: 1600px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

.filter__section {
    padding: 18px 79px;
    background: rgba(255, 255, 255, 0.8);
    font-size: 18px;
    line-height: 21px;
    font-weight: 400;
    text-align: center;
    height: 100%;
    cursor: pointer;
    &:hover {
        background: rgba(255, 255, 255, 1);
    }
    &.active {
        background: rgba(255, 255, 255, 1);
    }
    @media(max-width: 1600px) {
    }
    @include sm-block {
        display: none;
    }
}

.filter-sections-select {
    display: none;
    @include sm-block {
        display: block;
        margin-bottom: 10px;
        .choices__inner {
            min-height: 58px;
            padding: 18px 22px;
            border-radius: 10px;
            border: none;
        }
        .choices.is-open .choices__inner {
            border-radius: 10px 10px 0 0;
        }
        .choices__list--dropdown .choices__item {
            padding: 23px;
        }
        .choices.is-open .choices__list--dropdown {
            border: none;
            border-radius: 0 0 10px 10px;
            box-shadow: 0px 12px 33px rgba(0, 0, 0, 0.06);
        }
        .choices[data-type*=select-one] .choices__inner {
            padding-bottom: 18px;
        }
    }
}

.filter-tabs {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #ECECEC;
    @include md-block {
        overflow-x: auto;
        &::-webkit-scrollbar {
            display: none;
        }
    }
} 

.filter__tab {
    padding: 23px 35px;
    font-size: 14px;
    line-height: 16px;
    position: relative;
    white-space: nowrap;
    &::before {
        position: absolute;
        content: '';
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: $color-primary;
        opacity: 0;
    }
    &.active {
        &::before {
            opacity: 1;
        }
    }
}

.filter-row {
    display: grid;
    grid-template-columns: 1fr 1.2fr 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 38px 12px;
    @media(max-width: 1600px) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 18px 12px;
    }
    @include md-block {
        grid-template-columns: 1fr 1fr 1fr;
    }
    @include sm-block {
        grid-template-columns: 1fr;
        gap: 18px;
    }
}


.filter-wrapper {
    background-color: $color-white;
    border-radius: 10px;
}

.filter-inner {
    padding: 24px 33px 58px;
    @include sm-block {
        padding: 24px 22px 34px;
    }
}

.filter__checkboxes {
    display: inline-flex;
    border: 1px solid #ECECEC;
    border-radius: 4px;
    overflow: hidden;
}

.filter-item {
    width: 100%;
    @include sm-block {
        &--hidden {
            display: none;
        }
    }
}

.filter-item__row {
    display: flex;
    align-items: center;
}

.filter-item__title {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 10px;
    color: $color-text;
}

.filter__controls {
    grid-column: span 4;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    @include md-block {
        grid-column: span 3;
    }
    @include sm-block {
        grid-column: span 1;
        grid-template-columns: 1fr;
    }
}

.filter__btn-map,
.filter__btn-purchase {
    height: 66px;
    font-size: 16px;
    line-height: 66px;
}

.filter__btn-purchase {
    grid-column-end: -1;
}

.filter__btn-show  {
    display: none;
    @include sm-block {
        display: inline-block;
        margin: 0 auto;
        width: auto;
        display: block;
        text-align: center;
        color: #646464;
        position: relative;
        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 1px;
            bottom: 0;
            border-bottom: 1px dashed #646464;
        }
    }
}