.filter-buy {
    display: flex;
    @media(max-width: 1200px) {
        flex-direction: column;
    }
    @include sm-block {
        position: relative;
        padding: 24px;
        padding-top: 161px;
    }
}
.filter-buy__main {
    width: 100%;
    padding: 48px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    @include lg-block {
        padding: 24px;
    }
    @include sm-block {
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
    }
}

.filter-buy__img {
    max-width: 517px;
    width: 100%;
    img {
        width: 100%;
        display: block;
    }
    @media(max-width: 1360px) {
        max-width: 400px;
        display: block;
        margin: 0 auto;
    }
    @include xs-block {
        margin: 0 0 0 -24px;
    }
}

.filter-buy__info {
    max-width: 381px;
    min-width: 300px;
    width: 100%;
    margin-right: 24px;
    @include sm-block {
        position: absolute;
        max-width: 218px;
        top: 24px;
        left: 24px;
    }
}

.filter-buy__title {
    font-size: 40px;
    line-height: 50px;
    margin-bottom: 14px;
    @include sm-block {
        max-width: 190px;
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 14px;
    }
}

.filter-buy__desc {
    max-width: 290px;
    width: 100%;
    font-size: 16px;
    line-height: 26px;
    @include sm-block {
        max-width: 218px;
    }
}

.filter-buy__form {
    max-width: 332px;
    width: 100%;
    flex-shrink: 0;
}

