*,body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    line-height: 24px;
    color: $color-black;
}

h1,h2,h3,h4,h5,p {
    margin: 0;
    padding: 0;
}


.container {
    max-width: 1584px;
    padding: 0 40px;
    margin: 0 auto;
    @include md-block {
        padding: 0 20px;
    }
    @include sm-block {
        padding: 0 40px;
    }
}

ul {
    padding-left: 0;
}

li {
    padding-left: 0;
    list-style: none;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none; 
}