.complex {
    padding: 47px 0 49px;
}

.complex__title {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 28px;
}

.complex-main {

    padding-right: 381px;
    position: relative;
    margin-bottom: 47px;
    @include md-block {
        flex-direction: column;
        padding-right: 0;
    }
}

.complex__gallery {
    max-width: 1123px;
}

.complex-slider {
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    img {
        display: block;
        min-height: 611px;
        width: 100%;
        object-fit: cover;
    }
    @include sm-block {
        img {
            min-height: 257px;
        }
    }
}

.complex__side {
    max-width: 361px;
    flex-shrink: 0;
    padding: 36px;
    border: 1px solid #EDEDED;
    border-radius: 10px;
    position: absolute;
    right: 0;
    top: 0;
    @include md-block {
        position: static;
        max-width: 100%;
        padding: 24px;
    }
    @include sm-block {
        padding: 0;
        border: none;
    }
}

.complex__controls {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px 8px;
    margin-bottom: 37px;
    @include sm-block {
        grid-template-columns: 1fr;
        gap: 9px;
        margin-bottom: 17px;
    }
}

.complex__btn-request {
    grid-column: 1 / 3;
    @include sm-block {
        grid-column: inherit;
    }
}
.complex__btn-map {
    padding: 0 20px;
}
.complex__btn-call {
    padding: 0 20px;
}

.complex__complete {
    font-size: 15px;
    line-height: 29px;
    font-weight: 300;
    color: $color-text;
    margin-bottom: 43px;
    span {
        font-weight: 400;
        display: block;
        color: $color-black;
    }
    @include sm-block {
        margin-bottom: 17px;
    }
}

.complex-main__bottom  {
    display: flex;
    align-items: center;
    margin-top: 28px;
    @include md-block {
        margin: 23px 0 16px;
    }
    @include sm-block {
        flex-direction: column;
        align-items: flex-start;
    }
}

.complex__adress {
    display: flex;
    font-size: 15px;
    line-height: 18px;
    margin-right: 27px;
    svg {
        display: block;
        margin-right: 9px;
    }
    @include sm-block {
        margin-right: 0;
        margin-bottom: 17px;
    }
}
.complex__subway {
    display: flex;
    font-size: 15px;
    line-height: 18px;
    svg {
        display: block;
        margin-right: 9px;
    }
}
.complex-main__map {
    margin: 0 0 0 auto;
    @include md-block {
        display: none;
    }
}

.complex__subtitle {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 24px;
    @include sm-block {
        font-size: 18px;
        line-height: 21px;
        margin-bottom: 20px;
    }
}

.complex-details {
    max-width: 1123px;
    width: 100%;
}

.complex-chars {
    margin-bottom: 72px;
    @include sm-block {
        margin-bottom: 37px;
    }
}

.complex-chars__list {
    display: grid;
    grid-template-columns: .9fr 1fr;
    gap: 20px 36px;
    @include sm-block {
        grid-template-columns: 1fr;
        gap: 12px;
    }
}

.complex-chars__item {
    display: flex;
    font-size: 15px;
    line-height: 18px;
    font-weight: 400;
    span {
        display: block;
        margin-right: 8px;
        font-weight: 600;
    }
    @include md-block {
        display: block;
        span {
            display: inline;
        }
    }
    @include sm-block {
        font-size: 13px;
        line-height: 22px;
    }
}

.complex-objects {
    margin-bottom: 45px;
    @include sm-block {
        margin-bottom: 38px;
    }
}

.complex-objects__item {
    font-size: 15px;
    line-height: 24px;
    font-weight: 400; 
    @include sm-block {
        font-size: 13px;
        line-height: 22px;
        margin-bottom: 0;
    }
}

.complex-desc {
    margin-bottom: 45px;
    @include sm-block {
        margin-bottom: 48px;
    }
}

.complex-desc__text {
    font-size: 15px;
    line-height: 27px;
    margin-bottom: 23px;
    @include sm-block {
        font-size: 13px;
        line-height: 22px;
        margin-bottom: 21px;
    }
}

.complex-desc__btn {
    display: inline-flex;
    @include xs-block {
        width: 100%;
    }
}

.complex__apartments {
    margin-bottom: 63px;
}

.complex__apartments-pagination {
    margin-top: 16px;
    img {
        display: block;
        margin: 0 auto;
    }
}


.complex-tabs  {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #ECECEC;
    margin-bottom: 31px;
    overflow-x: auto;
    &::-webkit-scrollbar {
        display: none;
    }
}

.complex__tab {
    padding: 23px 38px;
    position: relative;
    font-size: 14px;
    line-height: 17px;
    white-space: nowrap;
    cursor: pointer;
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background: $color-primary;
        opacity: 0;
    }
    &.active {
        &::before {
            opacity: 1;
        }
    }
}

.complex-tab-content {
    display: none;
    &.active {
        display: block;
    }
}

.complex__video {
    position: relative;
    width: 100%;
    height: 518px;
    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    @include sm-block {
        height: 512px;
    }
}

.complex__video-btn {
    position: absolute;
    width: 100px;
    height: 100px;
    background-color: $color-white;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
        opacity: .8;
        
    }

}

.complex__bottom-map {
    height: 518px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
    }
}

.complex-filter  {
    padding: 60px;
    box-shadow: 0px 12px 33px rgba(0, 0, 0, 0.06);
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 57px;
    .filter-inner {
        padding: 0;
    }
    .filter-row {
        grid-template-columns: 1fr 1fr 1fr 1fr 1.4fr;
        gap: 38px 12px;
    }
    .filter__controls {
        display: block;
        grid-column: span 1;
    }
    .filter__btn-purchase {
        font-size: 14px;
        line-height: 16px;
        width: 100%;
        line-height: 67px;
    }
    @include lg-block {
        .filter-row {
            grid-template-columns: 1fr 1fr 1fr;
        }
        .filter__btn-purchase {
            grid-column: span 2;
        }
    }

    @include sm-block {
        padding: 27px 22px 33px;
        .filter-row {
            grid-template-columns: 1fr;
        }
        .filter__btn-purchase {
            grid-column: span 1;
        }
    }
}

.complex-filter__title {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 30px;
    @include sm-block {
        font-size: 18px;
        line-height: 22px;
    }
}