.form__input {
    width: 100%;
    padding: 12px;
    height: 48px;
    font-size: 13px;
    line-height: 15px;
    color: $color-black;
    text-align: center;
    background: #fff;
    border: 1px solid #ECECEC;
    border-radius: 4px;
    outline: none;
    &::placeholder {
        font-size: 13px;
        line-height: 15px;
        color: $color-gray;
        text-align: left;
    }
    &:focus::placeholder {
        display: none;
        opacity: 0;
    }
    &--left {
        height: 40px;
        border-radius: 4px 0 0 4px;
        border: 1px solid #ECECEC;
        &::placeholder {
            text-align: center;
        }
    }
    &--right {
        height: 40px;
        border-radius: 0 4px 4px 0;
        border: 1px solid #ECECEC;
        &::placeholder {
            text-align: center;
        }
    }
}

.form__checkbox {
    &:last-of-type {
        .form__checkbox-lable {
            border-right: 0;
        }
    }
    &-label {
        display: block;
        font-size: 13px;
        line-height: 15px;
        padding: 12px 15px;
        height: 40px;
        border-right: 1px solid #ECECEC;
        text-align: center;
        cursor: pointer;
    }
    &-input {
        display: none;
        &:checked + .form__checkbox-label {
            background-color: $color-primary;
            color: $color-white;
        }
    }
}

.form-item {
    margin-bottom: 24px;
}

.form-item__title {
    margin-bottom: 10px;
}

.form__btn.btn {
    display: block;
    height: 66px;
    width: 100%;
}

.form-item__select {
    .choices__inner {
        min-height: 47px !important;
    }
}