.btn {
    display: inline-block;
    width: auto;
    font-size: 14px;
    line-height: 43px;
    font-weight: 600;
    color: $color-white;
    padding: 0 26px;
    height: 43px;
    border: none;
    outline: none;
    color: $color-white;
    background-color: $color-primary;
    cursor: pointer;
    border-radius: 8px;
    text-align: center;
    &:hover {
        opacity: .7;
    }
    &--secondary {
        background-color: $color-secondary;
        color: $color-primary;
        display: flex;
        align-items: center;
        justify-content: center;
        svg {
            display: block;
            margin-right: 7px;
        }
    }
}